<script>
import SwipeCards from '../components/SwipeCards';
import axios from 'axios';

export default {
  data() {
    return {
      drawer: null,
      icons: ['mdi-instagram'],
      projects: [],
      employees: [],
    };
  },
  components: {
    SwipeCards,
  },
  methods: {
    async getEmployees() {
      let res = await axios({
        url: 'http://127.0.0.1:3000/employees',
        method: 'get',
      });
      this.employees = res.data;
    },
    async getProjects() {
      let res = await axios({
        url: 'http://127.0.0.1:3000/projects',
        method: 'get',
      });
      this.projects = res.data;
    },
  },
  created() {
    this.getEmployees();
    this.getProjects();
  },
};
</script>
